___CSS_LOADER_EXPORT___.push([module.id, `body {
    background: linear-gradient(#005986, 90%, white);
    height: 100vh;
}

main {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
}

main,
button {
    font-family: 'Roboto';
}

.forecast {
    width: 200px;
    height: 300px;
    background: linear-gradient(135deg, rgb(97, 192, 255), white);
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.351);
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
}

.condition {
    font-size: .75rem;
    padding: 5px;
    font-weight: bold;
}

h2 {
    font-size: 3rem;
    color: white;
}

.date {
    font-size: 2rem;
    font-weight: bold;
    margin: 10px 0 0 0;
}

.precipitationContainer {
    padding: 5px;
    margin: 10px 0 10px 0;
    display: flex;
}

.precipitationContainer > div {
    margin-right: 5px;
}

.temperature {
    padding: 5px;
}

.weatherIcon {
    height: auto;
    width: 150px;
}

.container {
    display: grid;
    grid-template-columns: repeat(3, 200px);
    gap: 10px;
}

input:focus {
    outline: none;
}

form {
    display: flex;
    align-items: flex-start;
    color: white;
}

#inputContainer {
    margin: 0 5px 5px 5px;
    display: flex;
    flex-direction: column;
}

.error {
    display: none;
    color: white;
    background-color: red;
    border-radius: 0 0 5px 5px;
    padding: 3px;
    text-align: center;
}

.active {
    display: block;
}

fieldset {
    display: flex;
}

@keyframes content {
    from {
        transform: translateX(-1000px);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

.forecast {
    animation: content 1s cubic-bezier(0.42, 0, 0.75, 1.2) forwards;
}

.spinner {
    height: 100px;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 10px solid transparent;
    border-top-color: black;
    border-left-color: rgba(0, 0, 0, 0.66);
    border-bottom-color: rgba(0, 0, 0, 0.33);
    border-radius: 50%;
    animation: rotate 300ms infinite;
}

@keyframes rotate {
    from {
        transform: rotate(0);
    }
    to {
        transform: rotate(360deg);
    }
}

.hidden {
    display: none;
}`, "",{"version":3,"sources":["webpack://./src/css/style.css"],"names":[],"mappings":"AAAA;IACI,gDAAgD;IAChD,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;;IAEI,qBAAqB;AACzB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,6DAA6D;IAC7D,4CAA4C;IAC5C,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,qBAAqB;IACrB,aAAa;AACjB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,uCAAuC;IACvC,SAAS;AACb;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,YAAY;AAChB;;AAEA;IACI,qBAAqB;IACrB,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,qBAAqB;IACrB,0BAA0B;IAC1B,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI;QACI,8BAA8B;QAC9B,UAAU;IACd;IACA;QACI,wBAAwB;QACxB,UAAU;IACd;AACJ;;AAEA;IACI,+DAA+D;AACnE;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,8BAA8B;IAC9B,uBAAuB;IACvB,sCAAsC;IACtC,wCAAwC;IACxC,kBAAkB;IAClB,gCAAgC;AACpC;;AAEA;IACI;QACI,oBAAoB;IACxB;IACA;QACI,yBAAyB;IAC7B;AACJ;;AAEA;IACI,aAAa;AACjB","sourcesContent":["body {\n    background: linear-gradient(#005986, 90%, white);\n    height: 100vh;\n}\n\nmain {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    padding: 50px;\n}\n\nmain,\nbutton {\n    font-family: 'Roboto';\n}\n\n.forecast {\n    width: 200px;\n    height: 300px;\n    background: linear-gradient(135deg, rgb(97, 192, 255), white);\n    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.351);\n    border-radius: 5px;\n    display: flex;\n    flex-direction: column;\n    justify-content: start;\n    align-items: center;\n}\n\n.condition {\n    font-size: .75rem;\n    padding: 5px;\n    font-weight: bold;\n}\n\nh2 {\n    font-size: 3rem;\n    color: white;\n}\n\n.date {\n    font-size: 2rem;\n    font-weight: bold;\n    margin: 10px 0 0 0;\n}\n\n.precipitationContainer {\n